@mixin square($size) {
  width: $size;
  height: $size;
}

@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: 640px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onDesign {
  @media (min-width: 1600px) {
    @content;
  }
}

@mixin Large {
  @media (min-width: 2560px) {
    @content;
  }
}
